.list {
  position: relative;
  bottom: 10px;
  right: 4px;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.bottom {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  .custom-input {
    border-radius: 1px;
    background-color: #151515;
  }

  label {
    display: flex;
    align-items: center;
  }

  
  .box {
    width: 16px;
    height: 16px;
  }

  .text {
    position: relative;
    top: 4px;
    left: 5px;
    width: 100%;
  }

  input[type="text"] {
    color: white;
  }

  input[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    transform: scale(1);
    width: 16px;
    height: 16px;
    border: 1px solid #000;
    border-radius: 1px;
    background-color: #151515;
    color: white;
    cursor: pointer;
  }
  
  input[type="checkbox"]:checked {
    background-color: rebeccapurple;
  }

.status {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

  #statusFull {
    position: absolute;
    font-weight: bold;
    color: rebeccapurple;
    opacity: 0%;
  }

  #statusCount {
    position: relative;
    opacity: 100%;
  }

  .transition-statusCount {
    animation-name: log-in-2, fade-in, slide-down-2;
    animation-duration: 1.5s;
    animation-timing-function: ease-in-out;
  }


  .transition-statusFull {
    animation-name: horizontal-shaking, log-in-1, fade-out, slide-down-1;
    animation-duration: 1.5s;
    animation-timing-function: ease-in-out;
  }

  @keyframes slide-down-2 {
    75% { transform: translateY(0px) }
    100% { transform: translateY(2px) }
  }

  @keyframes slide-down-1 {
    75% { transform: translateY(1px) }
    87.5% { transform: translateY(2px) } 
    100% { transform: translateY(10px) }
  }

  @keyframes fade-out {
    75% {opacity: 75%}
    100% {opacity: 0%}
  }

  @keyframes fade-in {
    75% {opacity: 0%}
    100% {opacity: 75%}
  }

  @keyframes log-in-1 {
    0% { opacity: 100%}
    75% {opacity: 100%}
  }

  @keyframes log-in-2 {
    0% { opacity: 0%}
    0% { bottom: 1px}
    75% {opacity: 0%}
    75% {bottom : 1px}
  }

  @keyframes horizontal-shaking {
    0% { transform: translateX(0) }
    6.25% {transform: translateX(-2px)}
    12.5% { transform: translateX(2px) }
    18.75% { transform: translateX(-2px) }
    25% { transform: translateX(2px) }
    31.25% { transform: translateX(-2px) }
    37.5% { transform: translateX(2px) }
    43.75% { transform: translateX(-2px) }
    50% { transform: translateX(2px) }
    56.25% { transform: translateX(-2px) }
    62.5% { transform: translateX(2px) }
    75% { transform: translateX(0px) }
  }